.main-stock {
    margin-left: 200px;
    margin-top: 53px;
    padding: 0 10px;
}

.produit-form {
    padding: 0 0px 20px 0px;
}

.produit-form header {
    padding: 10px 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}

.produit-form h1 {
    font-size: 17px!important;
}

.produit-form .form-block:not(:last-child) {
    margin-bottom: 20px;
}

.produit-form .form-block {
    padding: 20px;
    border-radius: 7px;
    background-color: #fff;
    display: flex;
    border: 1px solid #d9d9d9;
}

.produit-form .form-block > h2 {
    /* font-size: 20px!important; */
    width: 30%;
    min-width: 200px;
}

.produit-form .block-title {
    color: #666;
    font-size: 16px!important;

}
.produit-form .form-block > div {
    flex: 1;
    margin-left: 20px;
}

.produit-form .form-actions {
    padding: 20px;
    margin-top: 10px;
}

.produit-form .form-actions > div {
    justify-content: flex-end;
}

.approv-container {
    padding-bottom: 20px;
}

/* Produits */
.produits {
    flex: 1 1;
    background-color: #fff;
    padding: 10px 15px;
    border-radius: 5px;
    border: 1px solid #d9d9d9;
    min-height: calc(100vh - 123px)
}